import React, { useState } from "react";
import Axios from "axios";

import "./Bar.css";

function Jokes() {
  const [joke, setJoke] = useState(" ");
  const [punch, setPunch] = useState(" ");

  const startRefresh = () => {
    fetch("https://official-joke-api.appspot.com/random_joke")
      .then((response) => response.json())
      .then((data) => {
        setJoke(data.setup);
        setPunch(data.punchline);
      });
  };

  return (
    <div className="jokesContainer">
      <div className="container">
        <div className="row">
          <h3 className="alert alert-danger">
            Fetching Data using the fetch Api
          </h3>
          <h4 className="alert alert-primary">
            <span className="alert alert-secondary">The Joke:</span> {joke}
          </h4>

          <h4 className="alert alert-primary">
            <span className="alert alert-secondary">The Punch Line:</span>{" "}
            {punch}
          </h4>

          <button
            className="btn btn-primary btn-lg btn-block"
            onClick={startRefresh}
          >
            Start/Refresh
          </button>
        </div>
      </div>
    </div>
  );
}

export default Jokes;
