import React from "react";

import { Link } from "react-router-dom";

import "./Bar.css";

function Nav() {
  return (
    <div className="materialContainer">
      <div className="container">
        <div className="row">
          <h3 className="col">
            <div className="alert alert-danger">Fetching data using:</div>
          </h3>
          <h3 className="col">
            <div className="alert alert-secondary">Fetch Api</div>
          </h3>
          <h3 className="col">
            <div className="alert alert-secondary">Using Axios</div>
          </h3>
          <div className="w-100"></div>
          <h3 className="col">
            <div></div>
          </h3>
          <Link className="col" to="/jokes">
            <h3 className="alert alert-warning">Random Jokes Api</h3>
          </Link>
          <Link className="col" to="/cats">
            <h3 className="alert alert-warning">Random Cat Facts</h3>
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Nav;
